body {
  background: #c2f784;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.main-section {
  background: transparent;
  max-width: 600px;
  width: 90%;
  height: 500px;
  margin: 30px auto;
}

.clock-holder {
  width: 100%;
  background: #ffedda;
  margin: 30px auto;
  position: relative;
}

.stopwatch {
  padding: 60px 0px;
  text-align: center;
}
.stopwatch span {
  background: #7fc8a9;
  color: #ffedda;
  display: inline-block;
  font-family: monospace;
  font-size: 22px;
  padding: 20px;
  border-radius: 5px;
  width: 70px;
}

.stopwatch .stopwatch-btn {
  background: #ffedda;
  margin-top: 30px;
  margin-left: 5px;
  border: none;
  padding: 12px 20px;
  font-size: 16px;
  text-transform: uppercase;
  width: 150px;
  transition: background 1s;
  cursor: pointer;
}
.stopwatch .stopwatch-btn-red {
  border: 2px solid #ff6b6b;
  color: #ff6b6b;
}
.stopwatch .stopwatch-btn-yel {
  border: 2px solid #ff67e7;
  color: #ff67e7;
}

.stopwatch .stopwatch-btn-gre {
  border: 2px solid #7fc8a9;
  color: #7fc8a9;
}
.stopwatch .stopwatch-btn-red:hover {
  background: #ff6b6b;
  border: 2px solid #ff6b6b;
  color: #ffedda;
}
.stopwatch .stopwatch-btn-yel:hover {
  background: #ff67e7;
  border: 2px solid #ff67e7;
  color: #ffedda;
}
.stopwatch .stopwatch-btn-gre:hover {
  background: #7fc8a9;
  border: 2px solid #7fc8a9;
  color: #ffedda;
}
